.Home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90vh;
}

#infoWrapper {
  display: flex;
  width: 50vw;
  height: 40vh;
}

#infoText {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-wrap: wrap;
  width: "50%";
}

#infoText p {
  font-size: 20px;
}

.linkBtn {
  width: 50px;
  height: 50px;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  -webkit-user-drag: none;
}

#FAQ {
  width: 50vw;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.faqDetails {
  width: 50vw;
  height: 10vh;
  font-size: 20px;
  cursor: text;
}

.faqSummary {
  cursor: pointer;
}
