.archive-container {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.archive-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-top: 2rem;
}

.archive-item {
  flex: 1 1 300px;
  padding: 1.5rem;
  border: 1px solid #eee;
  border-radius: 8px;
  background-color: white;
  transition: transform 0.2s ease;
  text-decoration: none;
}

.archive-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.archive-item h2 {
  margin: 0 0 1rem 0;
  font-size: 1.5rem;
}

.archive-item .date {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.archive-item .excerpt {
  color: #333;
  margin-bottom: 1rem;
}

.archive-item a {
  color: #007bff;
  text-decoration: none;
}

.archive-item a:hover {
  text-decoration: underline;
}
